/* Futuristic Minimalist Landing Page */
.landing-futuristic {
    min-height: 100vh;
    background: var(--background-light);
    padding-top: 70px;
    /* Account for fixed navbar */
}

.container-futuristic {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 24px;
}

/* Hero Section */
.hero-section {
    min-height: calc(100vh - 70px);
    display: flex;
    align-items: center;
    position: relative;
    overflow: hidden;
}

.hero-content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 80px;
    align-items: center;
    width: 100%;
}

.hero-text {
    max-width: 500px;
}

.hero-title {
    font-size: clamp(3rem, 8vw, 4.5rem);
    font-weight: 200;
    line-height: 1.1;
    margin-bottom: 24px;
    color: var(--text-color);
}

.hero-title .highlight {
    background: var(--gradient-primary);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    font-weight: 300;
}

.hero-subtitle {
    font-size: 1.25rem;
    color: var(--text-light);
    line-height: 1.6;
    margin-bottom: 40px;
    font-weight: 300;
}

.hero-actions {
    display: flex;
    gap: 16px;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
}

.hero-actions .btn-minimal {
    margin: 0;
    flex-shrink: 0;
}

/* Hero Visual */
.hero-visual {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.floating-elements {
    position: absolute;
    width: 100%;
    height: 100%;
    pointer-events: none;
}

.floating-circle {
    position: absolute;
    border-radius: 50%;
    background: var(--gradient-secondary);
    opacity: 0.6;
    animation: float 6s ease-in-out infinite;
}

.circle-1 {
    width: 80px;
    height: 80px;
    top: 20%;
    left: 10%;
    animation-delay: 0s;
}

.circle-2 {
    width: 60px;
    height: 60px;
    top: 60%;
    right: 20%;
    animation-delay: 2s;
}

.circle-3 {
    width: 40px;
    height: 40px;
    bottom: 20%;
    left: 20%;
    animation-delay: 4s;
}

@keyframes float {

    0%,
    100% {
        transform: translateY(0px) rotate(0deg);
    }

    50% {
        transform: translateY(-20px) rotate(180deg);
    }
}

.hero-image {
    position: relative;
    z-index: 2;
}

.image-placeholder {
    width: 400px;
    height: 400px;
    background: var(--glass-bg);
    backdrop-filter: blur(20px);
    border: 1px solid var(--glass-border);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: var(--shadow-medium);
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.image-placeholder:hover {
    transform: scale(1.05);
    box-shadow: var(--shadow-heavy);
}

.placeholder-content {
    text-align: center;
    color: var(--text-light);
}

.placeholder-icon {
    font-size: 4rem;
    margin-bottom: 16px;
    display: block;
}

.animated-flower {
    animation: flowerFloat 3s ease-in-out infinite;
}

@keyframes flowerFloat {

    0%,
    100% {
        transform: translateY(0px) rotate(0deg) scale(1);
    }

    25% {
        transform: translateY(-8px) rotate(5deg) scale(1.05);
    }

    50% {
        transform: translateY(-15px) rotate(0deg) scale(1.1);
    }

    75% {
        transform: translateY(-8px) rotate(-5deg) scale(1.05);
    }
}

.placeholder-content span {
    font-size: 1.1rem;
    font-weight: 300;
}

.placeholder-text {
    font-size: 1.1rem;
    font-weight: 300;
}

/* Medical Disclaimer Section */
.medical-disclaimer-section {
    padding: 40px 0;
    background: var(--background-color);
    border-top: 1px solid var(--border-color);
}

.medical-disclaimer-content {
    display: flex;
    align-items: flex-start;
    gap: 20px;
    background: rgba(255, 193, 7, 0.1);
    border: 2px solid rgba(255, 193, 7, 0.3);
    border-radius: 12px;
    padding: 24px;
    max-width: 800px;
    margin: 0 auto;
}

.medical-disclaimer-icon {
    font-size: 2rem;
    color: #ff6b35;
    flex-shrink: 0;
    margin-top: 4px;
}

.medical-disclaimer-text h3 {
    color: #856404;
    font-size: 1.2rem;
    font-weight: 600;
    margin-bottom: 12px;
    display: flex;
    align-items: center;
    gap: 8px;
}

.medical-disclaimer-text p {
    color: #856404;
    font-size: 0.95rem;
    line-height: 1.5;
    margin: 0;
    font-weight: 400;
}

/* Mobile responsive for medical disclaimer */
@media (max-width: 768px) {
    .medical-disclaimer-section {
        padding: 30px 0;
    }

    .medical-disclaimer-content {
        flex-direction: column;
        gap: 16px;
        padding: 20px;
        margin: 0 16px;
    }

    .medical-disclaimer-icon {
        font-size: 1.5rem;
        margin-top: 0;
    }

    .medical-disclaimer-text h3 {
        font-size: 1.1rem;
        margin-bottom: 10px;
    }

    .medical-disclaimer-text p {
        font-size: 0.9rem;
    }
}

@media (max-width: 480px) {
    .medical-disclaimer-content {
        padding: 16px;
        margin: 0 12px;
    }

    .medical-disclaimer-text h3 {
        font-size: 1rem;
    }

    .medical-disclaimer-text p {
        font-size: 0.85rem;
    }
}

/* Features Section */
.features-section {
    background: var(--background-white);
}

.section-header {
    text-align: center;
    margin-bottom: 60px;
}

.section-title {
    font-size: clamp(2rem, 5vw, 3rem);
    font-weight: 200;
    margin-bottom: 16px;
    color: var(--text-color);
}

.section-subtitle {
    font-size: 1.1rem;
    color: var(--text-light);
    max-width: 600px;
    margin: 0 auto;
    font-weight: 300;
}

.feature-card {
    text-align: center;
    padding: 40px 24px;
    height: 100%;
}

.feature-icon {
    margin-bottom: 24px;
}

.icon-circle {
    width: 80px;
    height: 80px;
    background: var(--gradient-primary);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
    margin: 0 auto;
    box-shadow: var(--shadow-light);
}

.feature-card h3 {
    font-size: 1.5rem;
    font-weight: 300;
    margin-bottom: 16px;
    color: var(--text-color);
}

.feature-card p {
    color: var(--text-light);
    line-height: 1.6;
    font-weight: 300;
}

/* CTA Section */
.cta-section {
    background: var(--gradient-secondary);
}

.cta-content {
    text-align: center;
    padding: 60px 40px;
    max-width: 600px;
    margin: 0 auto;
}

.cta-content h2 {
    font-size: 2.5rem;
    font-weight: 200;
    margin-bottom: 16px;
    color: var(--text-color);
}

.cta-content p {
    font-size: 1.1rem;
    color: var(--text-light);
    margin-bottom: 32px;
    font-weight: 300;
}

/* Responsive Design */
@media (max-width: 1024px) {
    .container-futuristic {
        padding: 0 20px;
    }

    .hero-content {
        grid-template-columns: 1fr;
        gap: 60px;
        text-align: center;
    }

    .hero-text {
        max-width: 100%;
    }

    .image-placeholder {
        width: 300px;
        height: 300px;
    }

    .placeholder-icon {
        font-size: 3rem;
    }
}

@media (max-width: 768px) {
    .container-futuristic {
        padding: 0 16px;
    }

    .landing-futuristic {
        padding-top: 60px;
    }

    .hero-section {
        min-height: calc(100vh - 60px);
        padding: 40px 0;
    }

    .hero-content {
        gap: 40px;
    }

    .hero-title {
        font-size: clamp(2.5rem, 10vw, 3.5rem);
    }

    .hero-subtitle {
        font-size: 1.1rem;
    }

    .hero-actions {
        justify-content: center;
    }

    .image-placeholder {
        width: 250px;
        height: 250px;
    }

    .placeholder-icon {
        font-size: 2.5rem;
        margin-bottom: 0;
    }

    .placeholder-text {
        display: none;
    }

    .section-minimal {
        padding: 60px 0;
    }

    .feature-card {
        padding: 30px 20px;
    }

    .cta-content {
        padding: 40px 24px;
    }

    .cta-content h2 {
        font-size: 2rem;
    }

    .floating-circle {
        display: none;
    }
}

@media (max-width: 480px) {
    .container-futuristic {
        padding: 0 12px;
    }

    .hero-actions {
        flex-direction: column;
        align-items: center;
    }

    .image-placeholder {
        width: 200px;
        height: 200px;
    }

    .placeholder-icon {
        font-size: 2rem;
    }
}